import React, { useEffect, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrainSubway } from '@fortawesome/free-solid-svg-icons';
import { subwayInfo } from './interface';


export const Subway = ({subway}:any) => {
    
    const [info, setInfo] = useState<subwayInfo>(
        {   subwayId:'',
            bstatnNm:'',
            btrainSttus:'',
            updnLine:'',
            arvlMsg2:'',
            arvlMsg3:'',
            color:'',
            fastcolor:''
        }
    )


    useEffect(()=>{
        let linecolor:string = ''
        let fastcolor:string = 'black'
        let detailinfo:string = ''
        let linenumber:string = ''
        let time:string = ''
        switch (subway.subwayId[0]) {
            case '1':
                linecolor = '#0032A0';
             break;
            case '2':
                linecolor = '#00B140'
             break;
            case '3':
                linecolor = '#FC4C02'
             break;
            case '4':
                linecolor = '#00A9E0'
             break;
            case '5':
                linecolor = '#996CAC'
             break;
            case '6':
                linecolor = '#A9431E'
             break;
            case '7':
                linecolor = '#747F00'
             break;
            case '8':
                linecolor = '#E31C79'
             break;
            case '9':
                linecolor = '#8C8279'
             break;
            case '':
                linecolor = '#77C4A3'
             break;
            case '':
                linecolor = '#0065B3'
             break;
            case '':
                linecolor = '#0C8E72'
             break;
            case '':
                linecolor = '#F5A200'
             break;
            case '':
                linecolor = '#D4003B'
             break;
            case '':
                linecolor = '#B0CE18'
             break;
            case '':
                linecolor = '#8FC31F'
             break;
            case '':
                linecolor = '#003DA'
             break;
            case 'GTX-A':
                linecolor = '#9A6292'
             break;
            case '':
                subway.subwayId[0] = ''
                linecolor = '#6789CA'
             break;
 
        }
        if(subway.btrainSttus[0] === ''){
            fastcolor = 'red'
        }
        if(subway.arvlMsg2[0].includes('')){
            detailinfo = subway.arvlMsg2[0].split(" ");
            subway.arvlMsg2[0] = detailinfo[1]
        } else if(subway.arvlMsg2[0].includes('')){
            detailinfo = subway.arvlMsg2[0].split(" ");
            subway.arvlMsg2[0] = detailinfo[1]
        } else if (subway.arvlMsg2[0].includes('')){
            detailinfo = subway.arvlMsg2[0].split(" ");
            subway.arvlMsg2[0] = detailinfo[1]
        } else if (subway.arvlMsg2[0].includes('')){
            linenumber = subway.arvlMsg2[0].replace(/\D/g, '');
            subway.arvlMsg2[0] = `(${linenumber} )`
        } else if(subway.arvlMsg2[0].includes('')){
            time = subway.arvlMsg2[0].replace(/\s?\(.*\)/, "");
            subway.arvlMsg2[0] = `${time} `
        }
       
        setInfo({
            subwayId:subway.subwayId[0],
            bstatnNm:subway.bstatnNm[0],
            btrainSttus:subway. btrainSttus[0],
            updnLine:subway.updnLine[0],
            arvlMsg2:subway.arvlMsg2[0],
            arvlMsg3:subway.arvlMsg3[0],
            color:linecolor,
            fastcolor:fastcolor
        })
    },[subway])

return(
    <div className='subway_result'>
        <ul className='resultlist' style={{'display':'flex'}}>
            <li className='subwayemoji'>
                <FontAwesomeIcon icon={faTrainSubway}  className='emoji' />
            </li>
            <li className='subwayinfo'>
                <ul className='information'>
                    <li className='subwaylinebottom'><p className='subwayline'><span style={{color:info.color}}>{info.subwayId}</span> {info.bstatnNm} <span style={{color:info.fastcolor}}>{info.btrainSttus}</span> ({info.updnLine}) </p></li>
                    <li><p className='subwayarrive'> {info.arvlMsg3} {info.arvlMsg2}</p>
                    </li>
                </ul>
            </li>
        </ul>
    </div>
  );
};