import React, { useRef, useState, useEffect } from 'react';
import '../css/header.css'
import { config } from '@fortawesome/fontawesome-svg-core';
import '@fortawesome/fontawesome-svg-core/styles.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { library } from '@fortawesome/fontawesome-svg-core'
import { fas } from '@fortawesome/free-solid-svg-icons'
import { faTrainSubway, faMagnifyingGlass} from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from 'react-router-dom';
import { useStationStore } from '../stores/StationStore';
import { useTimeStore } from '../stores/TimeStore'; 



config.autoAddCss = false
library.add(fas)

const Header = () => {
  const searchRef = useRef(null)
  const navigator = useNavigate()
  const [result, setResult] = useState<string>('')
  const {stname,setStname} = useStationStore();
  const clearUserStation = useStationStore.persist.clearStorage;
  const {setNowDate} = useTimeStore();


  const handleChange = (text:React.ChangeEvent<HTMLInputElement>) => {
    setResult(text.target.value)
  };
  const handleStation = (event:React.MouseEvent<SVGSVGElement>)=>{
    if(result === ''){
      alert('    ')
      navigator('/')
    }
    else {
      navigator('/result')
      setStname(result)
      setNowDate(new Date())
    }
  }
  const handleKeyDown = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event.key === 'Enter') {
      if(result === ''){
        alert('    ')
        navigator('/')
      } else{
        navigator('/result')
        setStname(result)
        setNowDate(new Date())
      }
    }
  }
  const goingHome = ()=>{
    navigator('/')
    setResult('')
    setStname('')
    clearUserStation()
  }
  

return(
    <>
      <div id='header' className='header'>
        <ul className='header_title'>
          <li>
            <ul className='title'>
              <li><FontAwesomeIcon className='trainemoji' icon={faTrainSubway} 
                    onClick={goingHome}/></li>
              <li><h1 className='header_title_text'>   </h1></li>
            </ul>
          </li>
          <li className='mobile'>
            <div className='header_function'>
                <div className='header_search_part'>
                  <input className='header_search' type="text" placeholder=' '
                          onChange={handleChange} ref={searchRef} onKeyDown={handleKeyDown} 
                          value={result}/>
                    <button className='header_search_btn'>
                      <FontAwesomeIcon icon={faMagnifyingGlass} size='2x' 
                      onClick={handleStation}/>
                    </button>
                </div>
              </div>
            </li>
        </ul>
      </div>
    </>
  );
}
export default Header